import './CalendarDays.css';
const CalendarDays = () => {
  const days = [];
  const day = ['', '', '', '', '', '', ''];
  day.map((d, idx) => {
    days.push(
      <div className="day" key={idx}>
        {d}
      </div>
    );
  });
  return <div className="days">{days}</div>;
};
export default CalendarDays;
